<template>
  <div id="app">
    <b-container fluid class="px-0 overflow-hidden">
      <b-row class="mx-0">
        <b-col sm="12" class="px-0">
          <b-navbar variant="primary" v-if="showStickyBar() && strip" sticky>
            <p class="mx-auto mb-0 text-light">You are previewing the page from 500apps. You can use your own domain to remove this message.</p>
          </b-navbar>
          <router-view />
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>
<script>
  export default {
    name: "App",
    data() {
      return {
        host: window.location.host,
        strip: true,
      };
    },
    watch: {
      $route: {
        handler: (to) => {
          document.title = to.meta.title || "500apps - Grow like the Fortune 500";
          const link = document.querySelector("[rel='icon']");
          const favicon = `https://cdn.${to.meta.icon}/images/favicon.ico` || "https://infinity.500apps.com/img/infinity.svg";
          link.setAttribute("href", favicon);
        },
        immediate: true,
      },
    },
    mounted() {
      // this.host = "sign-dev-1744-3172.public.500apps.org";
      // Hide strip when location queryparams has type is widget
      const params = new URLSearchParams(window.location.search);
      let condition = window.location.href.includes("conference") || window.location.href.includes("ninjacapture") || window.location.href.includes("schedule");
      if (params.get("type") == "widget" || condition) this.strip = false;
    },

    methods: {
      // Restrict sticky bar for cname & 404 pages
      showStickyBar() {
        try {
          // Regular expression accepts all regions dev, qa,production for public URL
          let regex = /public(.*?).500apps/gm;
          return regex.test(this.host.toLowerCase()) && this.$router.history.current.name && this.$router.history.current.name != "404 page";
        } catch (error) {
          console.log(error);
        }
      },
    },
  };
</script>
<style lang="scss"></style>
