import Vue from "vue";

import router from "@/router/index";
import App from "@/App.vue";
import { BootstrapVue } from "bootstrap-vue";
import axios from "axios";
import store from "../dialer/twilio";
import { decompressFromEncodedURIComponent as lzStringDecompress } from "lz-string";

Vue.use(BootstrapVue);

const Init = async () => {
  // URL will come as {app}-{region}-{env}-{domain}-{user-id_optional}
  let host = window.location.host;
  // URL for cname testing purpose added
  // let host = "schedule500apps.appup.ch";
  // URL for public page tetsing purpose added
  // let host = "forms-dev-2757-31.public-dev.500apps.org";

  // Regular expression accepts all regions dev, qa,production for public URL
  let regex = /public(.*?).500apps/gm;

  // Decompress ninjacapture token ,which is from location path
  if (host.includes("drive.500apps.com") || host.includes("ninjacapture")) {
    host = window.location.href.includes("/nc") ? lzStringDecompress(location.pathname.split("/")[2]) : lzStringDecompress(location.pathname.split("/")[1]);
  } else if (!regex.test(host.toLowerCase()) && host.includes(".conference.ly")) {
    loadRouter();
  } else if (!regex.test(host.toLowerCase())) {
    var response = await axios.get(`https://dns.google/resolve?name=${host}&type=cname`);
    // Navigate 404 route based on dns server response.It shoud be vary from app to app
    if (!response.data || !response.data.Answer || response.data.Answer.length == 0) {
      // Redirecting to 404 page
      window.history.pushState("", "", `/404`);
      return;
    }
    // Set the actual host for c-name pages
    host = response.data.Answer[0].name;
  }

  window.app = host.split("-")[0].toLowerCase();

  window.region = host.split("-")[1];
  // window.env = host.split("-")[2];
  window.domain = host.split(".")[0].split("-")[2];

  // Optional
  try {
    window.user = host.split(".")[0].split("-")[3];
  } catch (error) {
    console.log(error);
  }
  // Optional id
  try {
    window.id = host.split(".")[0].split("-")[4];
  } catch (error) {
    console.log(error);
  }

  // Redirecting to app page if there is no pathname in url
  if (window.app && window.location.pathname == "/" && !host.includes(".conference.ly")) {
    window.history.pushState("", "", `/${window.app}`);
  }
  loadRouter();
};

// If c-name exists then loading routes after getting c-name response
// It works for public pages also
const loadRouter = () => {
  new Vue({
    router,
    store,
    render: (h) => h(App),
  }).$mount("#app");
};

// Invoke init method
Init();
